import AccountCircleTwoToneIcon from '@mui/icons-material/AccountCircleTwoTone'
import CreditCardTwoToneIcon from '@mui/icons-material/CreditCardTwoTone'
import ExitToAppIcon from '@mui/icons-material/ExitToApp'
import HowToRegTwoToneIcon from '@mui/icons-material/HowToRegTwoTone'
import StarTwoToneIcon from '@mui/icons-material/StarTwoTone'
import { Avatar, Box, Button, Card, CardHeader, Container, Divider, Grid, styled, Typography } from '@mui/material'
import axios, { AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { FC, useEffect, useState } from 'react'
import { useAuthHeader, useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'
import ConfirmDialog from 'src/components/ConfirmDialog'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import { MEMBER_VALUES } from 'src/data/MemberValues'
import { MemberStatus } from 'src/models/member'
import { PureLightTheme } from 'src/theme/schemes/PureLightTheme'

interface Profile {
  permission: MemberStatus
  level: string
  fee: boolean
  attendance: number
  totalAttendance: number
  createdAt: string
  deletedAt: string | null
  firedAt: string | null
}

interface InfoItem {
  Icon: any
  name: string
  content: string
  error?: boolean
  column?: boolean
}

const AvatarPrimary = styled(Avatar)(
  ({ theme }) => `
      background: ${theme.colors.primary.lighter};
      color: ${theme.colors.primary.main};
      width: ${theme.spacing(7)};
      height: ${theme.spacing(7)};
`
)

const AvatarError = styled(Avatar)(
  ({ theme }) => `
      background: ${theme.colors.error.lighter};
      color: ${theme.colors.error.main};
      width: ${theme.spacing(7)};
      height: ${theme.spacing(7)};
`
)

const InfoItem: FC<InfoItem> = ({ Icon, name, content, error = false, column = false }: InfoItem) => {
  return (
    <Box
      px={2}
      py={{ xs: 1, sm: 3 }}
      display="flex"
      alignItems="flex-start"
      flexDirection={column ? { xs: 'row', md: 'column' } : 'row'}
    >
      {error ? <AvatarError>{Icon}</AvatarError> : <AvatarPrimary>{Icon}</AvatarPrimary>}

      <Box
        pt={column ? { xs: 0, md: 2 } : 0}
        pl={column ? { xs: 2, md: 0 } : 2}
        flex={1}
        textAlign={column ? { xs: 'left', md: 'center' } : 'left'}
      >
        <Typography variant="h4" color={PureLightTheme.colors.secondary.main} mb={2}>
          {name}
        </Typography>

        <Typography variant="h3">{content}</Typography>
      </Box>
    </Box>
  )
}

const Profile: FC<void> = () => {
  const params = useParams()
  const [profile, setProfile] = useState<Profile>()
  const [isDialog, setIsDialog] = useState<boolean>()
  const authHeader = useAuthHeader()
  const { enqueueSnackbar } = useSnackbar()
  const navigate = useNavigate()
  const authUser = useAuthUser()

  const createdDate = profile && profile.createdAt ? new Date(profile.createdAt).toLocaleDateString() : '-'
  const firedDate = profile && profile.firedAt ? new Date(profile.firedAt).toLocaleDateString() : '-'

  const club = authUser()?.clubs.filter(c => c.id === parseInt(params.id))[0]

  const handleDelete = () => {
    axios
      .delete(`${process.env.REACT_APP_APIHOST}/club/member?clubId=${params.id}`, {
        headers: { Authorization: authHeader() }
      })
      .then(res => {
        if (res.data.message !== 'ok') throw new Error()

        enqueueSnackbar('정상적으로 탈퇴하셨습니다.', { variant: 'success' })
        navigate(`/club/${params.id}`)
      })
      .catch(err => {
        enqueueSnackbar(`${err.response?.data?.message}`, {
          variant: 'error'
        })

        setIsDialog(false)
      })
  }

  useEffect(() => {
    if (!club) {
      navigate(`/club/${params.id}`)
    }

    axios
      .get(`${process.env.REACT_APP_APIHOST}/member/status?clubId=${params.id}`, {
        headers: { Authorization: authHeader() }
      })
      .then((res: AxiosResponse<Profile>) => {
        setProfile(res.data)
      })
      .catch(err => {
        enqueueSnackbar(`${err.response?.data?.message}`, {
          variant: 'error'
        })
        navigate(`/club/${params.id}`)
      })
  }, [])

  return (
    <>
      <Helmet>
        <title>프로필</title>
      </Helmet>

      {club && (
        <PageTitleWrapper>
          <Grid container alignItems="center">
            <Grid item>
              <Typography variant="h3" component="h3" gutterBottom>
                {club.name}
              </Typography>
              <Typography variant="subtitle2">
                {authUser().name} 님이 {club.name}에서 활동한 내역입니다
              </Typography>
            </Grid>
          </Grid>
        </PageTitleWrapper>
      )}

      <Container maxWidth="lg">
        {profile && (
          <Card>
            <CardHeader title="내 정보" />
            <Divider />
            <Grid container spacing={{ xs: 1, sm: 2 }} justifyContent="space-evenly" mt={{ xs: 1 }}>
              <Grid item xs={12} sm={5}>
                <InfoItem Icon={<HowToRegTwoToneIcon />} name="가입일" content={createdDate} />
              </Grid>
              <Grid item xs={12} sm={5}>
                <InfoItem Icon={<ExitToAppIcon />} name="탈퇴일" content={firedDate} error />
              </Grid>
            </Grid>
            <Divider />
            <Grid container spacing={{ xs: 1, sm: 2 }} justifyContent="space-evenly" mt={{ xs: 1 }}>
              <Grid item xs={12} sm={4} md={3}>
                <InfoItem
                  Icon={<StarTwoToneIcon />}
                  name="권한"
                  content={MEMBER_VALUES.permission.filter(item => item.value === profile.permission)[0].display}
                />
                <InfoItem Icon={<AccountCircleTwoToneIcon />} name="등급" content={profile.level} />
              </Grid>
              <Grid
                item
                xs={12}
                sm={4}
                md={3}
                display="flex"
                justifyContent={{ xs: 'start', md: 'center' }}
                alignItems="center"
              >
                <InfoItem
                  Icon={<CreditCardTwoToneIcon />}
                  name="회비 납부"
                  content={profile.fee ? '납부' : '미납부'}
                  error={!profile.fee}
                  column
                />
              </Grid>
              <Grid
                item
                xs={12}
                sm={4}
                md={3}
                display="flex"
                justifyContent="center"
                textAlign="center"
                flexDirection="column"
              >
                <Grid container display="flex" alignItems="center" px={2} py={{ xs: 1, sm: 3 }}>
                  <Grid item xs={4} md={12}>
                    <Typography variant="h1">
                      {profile.attendance}
                      <Typography variant="h3" color="secondary" display="inline-block">
                        /{profile.totalAttendance}
                      </Typography>
                    </Typography>
                  </Grid>

                  <Grid item xs={4} md={12} mt={3}>
                    <Typography variant="h3" mb={2} textAlign={{ xs: 'left', md: 'center' }}>
                      출석
                    </Typography>
                    <Typography
                      variant="h4"
                      color={PureLightTheme.colors.secondary.main}
                      display={{ xs: 'none', md: 'inherit' }}
                    >
                      실 출석 수 / 전체 참여 회의
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Card>
        )}
      </Container>
      <Box textAlign="center" mt={3}>
        <Button
          variant="contained"
          color="error"
          onClick={() => {
            setIsDialog(true)
          }}
        >
          동아리 탈퇴
        </Button>
      </Box>
      <ConfirmDialog
        open={isDialog}
        handleClose={() => {
          setIsDialog(false)
        }}
        handleSubmit={handleDelete}
      />
    </>
  )
}

export default Profile
